import React, {Component} from 'react';
import styled from 'styled-components';
import { Link } from 'gatsby';
import posed from 'react-pose';

import Text from '../partials/Text';
import Button from '../partials/Button';

const PosedContainer = posed.div({
  visible: {
    bottom: '0px'
  },
  hidden: {
    bottom: '-200px'
  },
});

const Container = styled(PosedContainer)`
  width: 100vw;
  padding: 1rem;
  position: fixed;
  bottom: 0;
  background-color: ${({theme}) => theme.colors.darkBlueOpacity};
  z-index: 999;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
`;

const WhiteLink = styled(Link)`
  color: ${({theme}) => theme.colors.white};
`;

const WideText = styled(Text)`
  max-width: 600px;
  margin: 0;
`;

const CookiesButton = styled(Button)`
  margin: 1rem;
  height: 60px;
`;

class Cookies extends Component {
  constructor(props) {
    super(props);
    this.state = {cookiesBar: true};

    this.acceptCookies = this.acceptCookies.bind(this);
  }

  showCookies() {
    const cookies = decodeURIComponent(document.cookie).split(';');
    let policy;

    for(let i = 0; i < cookies.length; i++) {
      let c = cookies[i];
      while (c.charAt(0) === ' ') {
        c = c.substring(1);
      }
      if (c.indexOf('cookies=') === 0) { 
        policy = c.substring(8, c.length);
      }
    }

    if (policy) {
      this.setState(state => ({
        cookiesBar: !state.cookiesBar
      }));
    }
  }

  acceptCookies() {
    let date = new Date();

    date.setTime(date.getTime() + (30*24*60*60*1000));
    let expires = 'expires='+ date.toUTCString();
    document.cookie = 'cookies=accepted;' + expires + ';path=/';

    this.setState(state => ({
      cookiesBar: !state.cookiesBar
    }));
  }

  componentDidMount() {
    this.showCookies();
  }

  render() {
    return (
      <Container pose={this.state.cookiesBar ? 'visible' : 'hidden'}>
        <WideText isWhite>Ta strona używa plików cookies. Sprawdź <WhiteLink to="/polityka-prywatnosci">politykę prywatności</WhiteLink>
        </WideText>
        <CookiesButton isWhite onClick={this.acceptCookies}>Zgadzam się</CookiesButton>
      </Container> 
    )
  }
}

export default Cookies
