import React, {Component} from 'react';
import styled from 'styled-components';

import H3 from '../partials/H3';
import Button from '../partials/Button';

const From = styled.form`
  max-width: 600px;
  box-shadow: 0px 10px 40px 0px ${({theme}) => theme.colors.shadow};
  margin: 3rem auto 0;
  padding: 3rem 2rem;
  text-align: left;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
`;

const Input = styled.input`
  border: ${({theme}) => theme.colors.blue} 1px solid;
  padding: 1rem;
  display: block;
  margin: .5rem 0 1rem;
  width: 100%;
  font-size: .9rem;
  font-family: 'Montserrat';
  font-weight: 200;
  transition: all .5s cubic-bezier(0.075, 0.82, 0.165, 1);
  &.error {
    border: ${({theme}) => theme.colors.red} 1px solid;
  }
`;

const Message = styled.textarea`
  border: ${({theme}) => theme.colors.blue} 1px solid;
  padding: 1rem;
  display: block;
  margin: .5rem 0 1rem;
  width: 100%;
  font-size: .9rem;
  font-family: 'Montserrat';
  font-weight: 200;
  &.error {
    border: ${({theme}) => theme.colors.red} 1px solid;
  }
`;

class ContactForm extends Component {
  constructor(props) {
    super(props);
    this.state = {
      name: '',
      email: '',
      title: '',
      message: '',
      error: '',
    };
  }

  handleInputChange = event => {
    const target = event.target;
    const value = target.value;
    const name = target.name;

    !value ? target.classList.add('error') : target.classList.remove('error');

    this.setState({
      [name]: value,
    })
  }

  handleSubmit = event => {
    event.preventDefault();
    if(!this.state.name ||
      !this.state.email ||
      !this.state.title ||
      !this.state.message) {
      this.setState({
        error: 'Uzupełnij wszystkie pola'
      });
    } else {
      this.setState({
        error: ''
      });
      alert(`Welcome ${this.state.name}, ${this.state.company}!`)

    }
  }

  render() {
    return (
      <From onSubmit={this.handleSubmit}>
        <H3>Imię i Nazwisko *</H3>
        <Input
          type='text'
          name='name'
          placeholder= 'Twoje Imię i Nazwisko'
          value={this.state.name}
          onChange={this.handleInputChange}
          onBlur={this.handleInputChange}
        ></Input>
        <H3>Adress e-mail *</H3>
        <Input
          type='email'
          name='email'
          placeholder= 'Twój adres e-mail'
          value={this.state.email}
          onChange={this.handleInputChange}
          onBlur={this.handleInputChange}
        ></Input>
        <H3>Tytuł wiadomości *</H3>
        <Input
          type='text'
          name='title'
          placeholder= 'Tytuł'
          value={this.state.title}
          onChange={this.handleInputChange}
          onBlur={this.handleInputChange}
        ></Input>
        <H3>Wiadomość *</H3>
        <Message
          type='text'
          name='message'
          placeholder= 'Treść'
          rows = '5'
          value={this.state.message}
          onChange={this.handleInputChange}
          onBlur={this.handleInputChange}
        ></Message>
        <H3 isRed>{this.state.error}</H3>
        <Button type='submit'>Wyślij</Button>  
      </From>
    )
  }
}

export default ContactForm;